body {
  margin: 0;
}

.stars {
  display: flex;
}

.stars__star {
  height: 16px;
  width: 16px;
  margin-right: 4px;
  background: url(images/star-active.svg) 80% no-repeat;
}
  
.stars--0 .stars__star,
.stars--1 .stars__star:nth-child(n + 2),
.stars--2 .stars__star:nth-child(n + 3),
.stars--3 .stars__star:nth-child(n + 4),
.stars--4 .stars__star:nth-child(n + 5) {
  background: url(images/star.svg) 80% no-repeat;
}
